* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  overflow: hidden;
  width: 100%;
  height: 100vh;
  margin: 0;
  font-family: 'Roboto', sans-serif;
  background: #15244e !important;

  iframe{
    position: unset !important;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p,
  a {
    margin-bottom: 0;
  }
}

body,
.loader-container {
  background: linear-gradient(0deg, rgba(218, 233, 255, 1) 0%, rgba(255, 255, 255, 1) 100%);
  width: 100%;
  height: 100%;
  font-weight: 300;
  overflow-x: hidden;
}

.loader-container {
  position: relative;
  z-index: 99999999;
}

.loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  img {
    width: 100%;
    max-width: 150px;
    margin: 0 auto;
    animation: loading 2s infinite;
  }


  .loading-animate {
    font-weight: bold;
    font-size: 40px;
    text-transform: uppercase;
    color: #067BBC;
    letter-spacing: 44px;
    padding-left: 48px;
    display: block;

    span {
      margin-top: 1rem;
      display: inline-block;
      animation: wave-text 1s ease-in-out infinite;
      white-space: nowrap;
      text-align: center;

      &:nth-of-type(1) {
        animation-delay: 0.0s;
      }

      &:nth-of-type(2) {
        animation-delay: 0.1s;
      }

      &:nth-of-type(3) {
        animation-delay: 0.2s;
      }

      &:nth-of-type(4) {
        animation-delay: 0.3s;
      }

      &:nth-of-type(5) {
        animation-delay: 0.4s;
      }

      &:nth-of-type(6) {
        animation-delay: 0.5s;
      }

      &:nth-of-type(7) {
        animation-delay: 0.6s;
      }
    }
  }
}


@keyframes wave-text {
  0% {
    transform: translateY(0em);
  }

  60% {
    transform: translateY(-0.6em);
  }

  100% {
    transform: translateY(0em);
  }
}

@keyframes loading {
  0% {
    opacity: 1;
  }

  50% {
    opacity: 0.5;
  }

  100% {
    opacity: 1;
  }
}

